import React from "react";
import { useInView } from "react-hook-inview";
import Button from "../../ui/Button";

import "./Project.css";
export default function Project({ project }) {
  const [ref, isVisible] = useInView({
    threshold: 0.4,
  });
  const animation = isVisible
    ? "fade-in 0.3s forwards"
    : "fade-out 0.3s forwards";
  // useEffect(() => {
  //   console.log(isVisible);
  // }, [isVisible]);
  return (
    <div ref={ref} className="project">
      <div className="project-content" style={{ animation: animation }}>
        <img src={project.imageUrl} alt="" />
        <div className="project-details">
          <h3>{project.title}</h3>
          <div className="project-technologies">
            {project.technologies.map((tech, index) => (
              <img
                className="project-technology"
                key={index}
                src={tech}
                alt=""
              ></img>
            ))}
          </div>
          <p>{project.description}</p>
          <div className="project-cta">
            <Button variant="primary">See Live</Button>
            <Button variant="outline-primary">See Code</Button>
          </div>
        </div>
      </div>
    </div>
  );
}
