import React, { useState } from "react";
import { HashLink as Link } from "react-router-hash-link";
import Highlight from "./Highlight";
import "./Highlights.css";
import { usePortfolio } from "../../../context/PortfolioContext";
import { useWindowDimensions } from "../../../hooks/useWindowDimensions";
import CarouselItem from "./CarouselItem";

export default function Highlights() {
  const { width } = useWindowDimensions();
  const { highlights } = usePortfolio();
  const [highlightActive, setHighlightActive] = useState(0);
  const [index, setIndex] = useState(0);
  const [project, setProject] = useState(highlights[index]);

  const onSetActive = (index) => {
    setHighlightActive(index);
  };
  const nextHandler = () => {
    if (index === highlights.length - 1) {
      setProject(highlights[0]);
      setIndex(0);
    } else {
      setProject(highlights[index + 1]);
      setIndex(index + 1);
    }
  };
  const prevHandler = () => {
    if (index === 0) {
      setProject(highlights[highlights.length - 1]);
      setIndex(highlights.length - 1);
    } else {
      setProject(highlights[index - 1]);
      setIndex(index - 1);
    }
  };
  return (
    <div className="home-highlights">
      <div className="highlights-control">
        <h3>Highlights</h3>
        <Link to="#projects">
          See more <i className="fas fa-angle-right"></i>
        </Link>
      </div>
      {width <= 1000 ? (
        <div className="highlights-carousel">
          <CarouselItem project={project} />
          <span className="carousel-back">
            <i className="fas fa-chevron-left" onClick={prevHandler}></i>
          </span>
          <span className="carousel-next">
            <i className="fas fa-chevron-right" onClick={nextHandler}></i>
          </span>
        </div>
      ) : (
        <div className="highlights-expandable-card">
          {highlights.map((project, index) => (
            <Highlight
              project={project}
              key={index}
              onClick={() => onSetActive(index)}
              active={highlightActive === index ? true : false}
            />
          ))}
        </div>
      )}
    </div>
  );
}
