import axios from "axios";
import React, { useContext, createContext, useState, useEffect } from "react";
import Splash from "../components/ui/Splash";

const API_LINK = "https://nodemcportfolio.herokuapp.com/api/portfolio";
// const API_LINK = "http://localhost:7000/api/portfolio";

const PortfolioContext = createContext({
  general: {},
  projectHighlights: [],
  projects: [],
});

export const usePortfolio = () => useContext(PortfolioContext);

export const PortfolioProvider = ({ children }) => {
  const [general, setGeneral] = useState({});
  const [projects, setProjects] = useState([]);
  const [highlights, setHighlights] = useState([]);
  const [isLoading, setIsLoading] = useState(true);

  const getPortfolio = async () => {
    try {
      let response = await axios.get(API_LINK);
      let data = response.data;
      setGeneral(data.general[0]);
      setHighlights(data.projects.filter((project) => project.highlight));
      setProjects(data.projects.filter((project) => !project.highlight));
      setIsLoading(false);
    } catch (err) {
      console.log(err);
      setIsLoading(false);
    }
  };

  useEffect(() => {
    getPortfolio();
  }, []);
  const value = {
    general,
    projects,
    highlights,
  };
  return (
    <PortfolioContext.Provider value={value}>
      {isLoading ? <Splash /> : children}
    </PortfolioContext.Provider>
  );
};
