import React from "react";

import "./Input.css";

export default function Input({
  type,
  value,
  onChange,
  disabled,
  error = false,
  label,
  id,
  helperText,
  className,
  placeholder,
  fullWidth = false,
  startIcon,
  endIcon,
  multiline = false,
  row,
  size,
  defaultValue,
}) {
  return (
    <div
      className={`input ${disabled && "disabled"} ${error && "error"} ${
        fullWidth && "full-width"
      } ${className}`}
    >
      {label && <p className="label">{label}</p>}
      <div className={`input-holder ${fullWidth && "full-width"} ${size}`}>
        {startIcon && !multiline && (
          <i className={`start-icon ${startIcon}`}></i>
        )}
        {multiline ? (
          <textarea
            defaultValue={defaultValue}
            className="input-area"
            value={disabled ? "" : value}
            onChange={disabled ? () => {} : onChange}
            readOnly={disabled}
            rows={row}
            placeholder={placeholder}
            id={id}
          ></textarea>
        ) : (
          <input
            id={id}
            className="input-area"
            readOnly={disabled}
            type={type || "text"}
            autoComplete="off"
            value={value}
            onChange={disabled ? () => {} : onChange}
            defaultValue={defaultValue}
            placeholder={placeholder}
          ></input>
        )}
        {endIcon && !multiline && <i className={`end-icon ${endIcon}`}></i>}
      </div>
      {helperText && <p className="helper-text">{helperText}</p>}
    </div>
  );
}
