import React from "react";
import Ripples from "react-ripples";

import "./Button.css";

// const variants = ["text", "primary", "secondary", "danger"]
export default function Button({
  children,
  type,
  onClick,
  className,
  variant,
  disabled = false,
  startIcon,
  endIcon,
  fullWidth = false,
}) {
  return (
    <Ripples during={800}>
      <button
        className={`button ${variant} ${disabled && "disabled"} ${
          fullWidth && "full-width"
        } ${className}`}
        type={type || "button"}
        onClick={disabled ? () => {} : onClick}
      >
        {startIcon && <i className={`start-icon ${startIcon}`}></i>}
        {children}
        {endIcon && <i className={`end-icon ${endIcon}`}></i>}
      </button>
    </Ripples>
  );
}
