import React, { useState } from "react";
import Input from "../ui/Input";
import Button from "../ui/Button";
import { useAdmin } from "../../context/AdminContext";

import "./AdminGeneral.css";

export default function AdminGeneral() {
  const { general, updateGeneral, generalChangeHandler } = useAdmin();
  const [generalResponse, setGeneralResponse] = useState();

  const submitHandler = async (e) => {
    e.preventDefault();
    setGeneralResponse();
    let data = await updateGeneral(general);
    console.log(data.success);
    if (data.success === true) {
      setGeneralResponse("Update Successful!");
    } else {
      setGeneralResponse("Update Failed!");
    }
  };
  return (
    <div className="admin-general">
      <h3>General Information</h3>
      {generalResponse && <p className="general-response">{generalResponse}</p>}
      <form onSubmit={submitHandler}>
        <div className="general-inline">
          <Input
            label="Brand"
            id="brand"
            placeholder="Brand"
            fullWidth
            onChange={generalChangeHandler}
            value={general.brand}
          />
          <Input
            label="Name"
            id="name"
            placeholder="Name"
            fullWidth
            onChange={generalChangeHandler}
            value={general.name}
          />
        </div>
        <div className="general-inline">
          <Input
            label="Description"
            id="description"
            placeholder="Description"
            fullWidth
            onChange={generalChangeHandler}
            value={general.description}
          />
          <Input
            label="Resume URL"
            id="resumeUrl"
            placeholder="Resume URL"
            fullWidth
            onChange={generalChangeHandler}
            value={general.resumeUrl}
          />
        </div>
        <Input
          label="About"
          id="about"
          placeholder="About"
          multiline
          row={8}
          fullWidth
          onChange={generalChangeHandler}
          value={general.about}
        />
        <Button variant="primary" type="submit">
          Update
        </Button>
      </form>
    </div>
  );
}
