import React, { useState } from "react";
import Button from "../../ui/Button";
import Modal from "../../ui/Modal";

import "./Highlight.css";

export default function Highlight({ project, active, onClick }) {
  const [isModalOpen, setIsModalOpen] = useState(false);
  return (
    <div
      className={`highlight ${active && "highlight-active"}`}
      onClick={onClick}
    >
      <img src={project.imageUrl} alt="" />
      <div className="highlight-info">
        <h3>{project.title}</h3>
        <Button variant="primary" onClick={() => setIsModalOpen(true)}>
          Info
        </Button>
      </div>
      <Modal
        onClose={() => setIsModalOpen(false)}
        show={isModalOpen}
        className="highlight-modal"
      >
        <div className="highlight-modal-content">
          <img src={project.imageUrl} alt="" />
          <h3>{project.title}</h3>
          <div className="highlight-technologies">
            {project.technologies.map((tech, index) => (
              <img
                className="highlight-technology"
                key={index}
                src={tech}
                alt=""
              ></img>
            ))}
          </div>
          <p>{project.description}</p>
        </div>
        <div className="highlight-modal-cta">
          <Button variant="primary">See Live</Button>
          <Button variant="outline-primary">See Code</Button>
        </div>
      </Modal>
    </div>
  );
}
