import axios from "axios";
import React, { createContext, useContext, useState, useEffect } from "react";
import Splash from "../components/ui/Splash";

const AUTH_LINK = "https://nodemcportfolio.herokuapp.com/api/auth";
// const AUTH_LINK = "http://localhost:7000/api/auth";
const API_LINK = "https://nodemcportfolio.herokuapp.com/api/portfolio";
// const API_LINK = "http://localhost:7000/api/portfolio";

const AdminContext = createContext({
  user: {},
  login: () => {},
  logout: () => {},
  updateGeneral: () => {},
  generalChangeHandler: () => {},
  general: {},
  projectHighlights: [],
  projects: [],
});

export const useAdmin = () => useContext(AdminContext);

export const AdminProvider = ({ children }) => {
  const [user, setUser] = useState();
  const [general, setGeneral] = useState({});
  const [projects, setProjects] = useState([]);
  const [highlights, setHighlights] = useState([]);
  const [isLoading, setIsLoading] = useState(true);

  const getPortfolio = async () => {
    try {
      let response = await axios.get(API_LINK);
      let data = response.data;
      setGeneral(data.general[0]);
      setHighlights(data.projects.filter((project) => project.highlight));
      setProjects(data.projects.filter((project) => !project.highlight));
      setIsLoading(false);
    } catch (err) {
      console.log(err);
      setIsLoading(false);
    }
  };

  useEffect(() => {
    let currentUser = getUserLocalStorage();
    setUser(JSON.parse(currentUser));
    getPortfolio();
  }, []);

  const login = async (username, password) => {
    let { data } = await axios.post(AUTH_LINK + "/login", {
      username,
      password,
    });
    if (data.verified === false) {
      return { failed: true };
    } else {
      setUser(data);
      localStorage.setItem("currentUser", JSON.stringify(data));
      return { failed: false };
    }
  };
  const logout = () => {
    localStorage.removeItem("currentUser");
    setUser();
  };
  const updateGeneral = async () => {
    try {
      let { data } = await axios.post(API_LINK + "/updategeneral", {
        ...general,
        token: user.token,
      });
      setGeneral(data.updatedGeneral);
      return { success: true };
    } catch (err) {
      return { success: false };
    }
  };
  const generalChangeHandler = (e) => {
    const { id, value } = e.target;
    setGeneral((prevState) => {
      return { ...prevState, [id]: value };
    });
  };
  const value = {
    user,
    login,
    logout,
    general,
    projects,
    highlights,
    updateGeneral,
    generalChangeHandler,
  };
  return (
    <AdminContext.Provider value={value}>
      {isLoading ? <Splash /> : children}
    </AdminContext.Provider>
  );
};

const getUserLocalStorage = () => {
  let local = localStorage.getItem("currentUser");
  if (local) {
    return local;
  } else {
    return null;
  }
};
